import React from "react";
import Image from "next/image";
import 'bootstrap/dist/css/bootstrap.min.css';
import Card from 'react-bootstrap/Card';

interface JobCardProps {
    title: string;
    company: string;
    image: string;
    year: string;
    description: string;
}

export default function InfoCard({ title, company, image, year, description }: JobCardProps) {
    return (
        <Card>
            <Card.Img src={image}/>
            <Card.Body>
                <Card.Title>
                    {title} | {company}
                </Card.Title>
                <Card.Subtitle>{year}</Card.Subtitle>
                <Card.Text>{description}</Card.Text>
            </Card.Body>
        </Card>
    );
}