import React from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import Card from 'react-bootstrap/Card';

interface ProjectCardProps {
    title: string;
    projectType: string;
    description: string;
}

export default function ProjectCard({ title, projectType, description}: ProjectCardProps) {
    return (
        <Card>
            <Card.Body>
                <Card.Title>{title}</Card.Title>
                <Card.Subtitle>{projectType}</Card.Subtitle>
                <Card.Text>{description}</Card.Text>
            </Card.Body>
        </Card>
    );
}